.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite spin-minute;
  position: absolute;
  bottom: 50%;
  left: calc(50% - 1.5vmin);
  transform: translateX(-50%);
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite spin-second;
  position: absolute;
  bottom: 50%;
  left: calc(50% - .75vmin);
  transform: translateX(-50%);
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation-duration: 600s;
}

@keyframes spin-minute {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes spin-second {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.page {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}
